export default {
	onLoad() {

		this.showVideo = false;

		try {
			const comments = uni.getStorageSync("comments");
			if (comments) {
				this.commentsAll = comments;
				console.log("commentsAll", this.commentsAll)
			}
		} catch (e) {
			console.log(e);
		}
	},
	// 页面初次渲染完毕
	onReady() {
		// video上下文对象
		this.videoContext = uni.createVideoContext("myVideo")
	},
	data() {
		return {
			commentsAll: [], //全部评论
			videoSrc: "",
			videoContext: "",
			showVideo: true,
			labelindex: 0,
			labelList: [{
					name: '全部',
					number: 25,
					type: 'all'
				},
				{
					name: '好评',
					number: 23,
					type: 'good'
				},
				{
					name: '中评',
					number: 1,
					type: 'secondary'
				},
				{
					name: '差评',
					number: 1,
					type: 'poor'
				},
				{
					name: '有图',
					number: 12,
					type: 'img'
				},
				{
					name: '视频',
					number: 2,
					type: 'video'
				},
				{
					name: '追加',
					number: 2,
					type: 'append'
				}

			],

		}
	},
	methods: {
		taging(index) {
			this.labelindex = index;
		},
		showBigImg(image, images) {
			uni.previewImage({
				current: image,
				urls: images
			})
		},
		playVideo(path) {
			this.videoSrc = path;

			// 全屏播放
			this.$nextTick(() => {
				// 进入全屏播放
				this.videoContext.requestFullScreen({
					direction: 0 //正常竖向全屏
				})
			})

			this.showVideo = false;

		},
		videoControl(e) {

			if (e.detail.fullScreen) { // 全屏
				// 自动播放
				this.videoContext.play();
			} else {
				// 停止播放
				this.videoContext.stop()
				this.videoPause();
			}
		},
		videoPause() {
			this.videoSrc = '';

			this.showVideo = false

		}
	}
}
